<template>
  <el-descriptions class="margin-top" >
    <template slot="extra">
      <el-button type="primary" size="small">操作</el-button>
    </template>
    <el-descriptions-item label="编号" >
      <el-tag size="small">{{this.tableData.id}}</el-tag>
    </el-descriptions-item>
    <el-descriptions-item label="账号" ><el-tag size="small">{{this.tableData.account}}</el-tag></el-descriptions-item>
    <el-descriptions-item label="密码"><el-tag size="small">{{this.tableData.password}}</el-tag></el-descriptions-item>
    <el-descriptions-item label="权限">
      <el-tag size="small">
        {{this.tableData.permissions == 0 ?"普通用户":""}}
        {{this.tableData.permissions == 1 ?"管理员":""}}
        {{this.tableData.permissions == 2 ?"超级管理员":""}}
      </el-tag>
    </el-descriptions-item>
  </el-descriptions>
</template>

<script >
import axios from "axios";
export default {
  name: 'ThisUserData',
  components: {},
  data() {
    return {
      tableData: [],

    }
  },
  mounted() {
    axios.get("http://93rf36tz6467.vicp.fun:15427/getUserData?account="+window.sessionStorage["account"]).then(res => {
      this.tableData = res.data.data;
    })
  }

}
</script>

<style >
.my-label {
  background: #E1F3D8;
}

.my-content {
  background: #FDE2E2;
}
</style>